<template>
  <div id="index" ref="appRef">
    <dv-loading v-if="loading">Loading...</dv-loading>
    <div v-else class="bg">
      <header>三农业务运营管理平台</header>
      <main>
        <div class="left">
          <CenterLeft></CenterLeft>
        </div>
        <div class="center">
          <div class="map-container">
            <CenterMoney></CenterMoney>
            <CenterMap></CenterMap>
          </div>
        </div>
        <div class="right">
          <CenterRight></CenterRight>
        </div>
      </main>
      <footer>
        <Bottom></Bottom>
      </footer>
    </div>
  </div>
</template>

<script>
// import resetScreenFit from "../utils/resetScreenFit.js";
import resetScreenCover from "../utils/resetScreenCover.js";
import Bottom from "./footer.vue";
import CenterLeft from "./left.vue";
import CenterMap from "./map.vue";
import CenterMoney from "./CenterMoney.vue";
import CenterRight from "./right.vue";
export default {
  mixins: [resetScreenCover],
  data() {
    return {
      chartRef: null,
      loading: true,
    };
  },
  components: {
    Bottom,
    CenterLeft,
    CenterMap,
    CenterMoney,
    CenterRight,
  },
  async mounted() {
    this.$nextTick(this.cancelLoading);
  },

  methods: {
    cancelLoading() {
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },
  },
};
</script>

<style lang="scss" scoped>
@import "../assets/scss/index2.scss";

#index {
  position: absolute;
  left: 50%;
  top: 50%;
  transform-origin: 0 0;
  overflow-y: scroll;

  &::-webkit-scrollbar {
    width: 4px;
    height: 100px;
    /* 滚动条的宽度 */
  }

  &::-webkit-scrollbar-track {
    background: #f1f1f1;
    /* 滚动条轨道的颜色 */
  }

  &::-webkit-scrollbar-thumb {
    background: #888;
    /* 滚动条滑块的颜色 */
    border-radius: 10px;
    /* 滚动条滑块的圆角 */
  }

  &::-webkit-scrollbar-thumb:hover {
    background: #555;
    /* 鼠标悬停在滚动条滑块上时的颜色 */
  }
}
</style>
